<template>
  <div>
    <!-- 未登录显示页面 -->
    <div class="noLoginTipBox" v-if="isLogin">
      <span class="noLoginTips">(๑•̀ㅂ•́)و✧</span><br />
      <span class="noLoginTips">您还未登录呢</span>
      <div class="gotoLoginBtn"><span @click="gotoLogin">去登录</span></div>
    </div>
    <!-- 登录状态页面 -->
    <div class="contentBox" v-if="!isLogin">
      <!-- 用户信息展示区域 -->
      <div class="userInfoBox">
        <!-- 侧边头像区域 -->
        <div class="asideBox">
          <div class="userpicBox"><img :src="userInfo.userpic" alt="" /></div>
          <div class="editBtnBox">
            <el-button
              @click="changeAvatarVisible = true"
              type="info"
              icon="el-icon-edit"
              round
              >更换头像</el-button
            >
          </div>
        </div>
        <!-- 主体信息区域 -->
        <div class="mainBox">
          <div class="userInfo">
            <div>昵称：{{ userInfo.username }}</div>
            <div>ID：{{ userInfo.userid }}</div>
            <div>角色：{{ userInfo.rolename }}</div>
            <div>
              电话：{{
                userInfo.telephone === 'null' ||
                userInfo.telephone === '' ||
                userInfo.telephone === null
                  ? '暂无'
                  : userInfo.telephone
              }}
            </div>
            <div>
              邮箱：{{
                userInfo.email === 'null' ||
                userInfo.email === '' ||
                userInfo.email === null
                  ? '暂无'
                  : userInfo.email
              }}
            </div>
          </div>
          <div class="infoEditBtnBox">
            <el-button
              type="info"
              icon="el-icon-edit"
              round
              @click="showeditUserInfoDialog"
              >修改资料</el-button
            >
          </div>
        </div>
      </div>
    </div>
    <!-- 更换头像对话框 -->
    <el-dialog
      title="更换头像"
      :visible.sync="changeAvatarVisible"
      width="50%"
      @close="selectedCancel"
    >
      <!-- 头像列表 -->
      <div class="avatarList">
        <div
          @click="selectAvatar"
          class="avatarBox"
          v-for="item in avatarList"
          :key="item.index"
          ref="avatarBoxRef"
        >
          <img :src="item" alt="" ref="imgRef" />
          <div ref="selectedRef" class="selected"></div>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="cancelAvatar">取 消</el-button>
        <el-button type="primary" @click="changeAvatar">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 修改资料对话框 -->
    <el-dialog
      title="修改资料"
      :visible.sync="editUserInfoDialogVisible"
      width="50%"
      @close="editUserInfoDialogClose"
    >
      <!-- 表单区域 -->
      <el-form
        :model="newUserInfo"
        :rules="newUserInfoRule"
        ref="newUserInfoRef"
        label-width="100px"
      >
        <el-form-item label="昵称" prop="username">
          <el-input v-model="newUserInfo.username" clearable></el-input>
        </el-form-item>
        <el-form-item label="电话" prop="telephone">
          <el-input v-model="newUserInfo.telephone" clearable></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="newUserInfo.email" clearable></el-input>
        </el-form-item>
      </el-form>
      <!-- 底部按钮区域 -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="editUserInfoDialogClose">取 消</el-button>
        <el-button type="primary" @click="updateUserInfo">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import userpicPath from '../../assets/img/userPic/avatarList.js'

export default {
  data() {
    // 验证用户名的自定义规则
    var checkUsername = (rule, value, cb) => {
      // 验证邮箱的正则表达式
      const regUsername =
        /[`~!#$%^&*()_+=<>?:"{}|~！#￥%……&*（）={}|《》？：“”【】、；‘’，。、\s+]/
      if (regUsername.test(value)) {
        // 邮箱不合法，包含特殊字符
        return cb(new Error('用户名包含特殊字符，请输入合法的邮箱'))
      }
      // 邮箱合法
      cb()
    }
    // 验证邮箱的自定义规则
    var checkEmail = (rule, value, cb) => {
      // 验证邮箱的正则表达式
      const regEmail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/
      if (regEmail.test(value)) {
        // 合法的邮箱
        return cb()
      }
      // 邮箱不合法
      cb(new Error('请输入合法的邮箱'))
    }
    // 验证电话的自定义规则
    var checkTelephone = (rule, value, cb) => {
      const regTelephone = /^(?:(?:\+|00)86)?1\d{10}$/
      if (regTelephone.test(value)) {
        // 合法的电话
        return cb()
      }
      // 电话不合法
      cb(new Error('请输入合法的电话'))
    }
    return {
      isLogin: false,
      // 用户token
      ruyiBlogToken: '',
      // 用户头像存储路径
      ruyiBlogUserId: '',
      // 用户信息接收对象
      userInfo: {
        id: 0,
        userid: '',
        username: '',
        userpic: '',
        rolename: '',
        telephone: '',
        email: ''
      },
      // 控制更换头像对话框的显示与隐藏
      changeAvatarVisible: false,
      // 头像列表接收对象
      avatarList: {},
      // 用户选择的新的头像路径
      selectNewUserpic: '',
      // 控制修改资料对话框的显示与隐藏
      editUserInfoDialogVisible: false,
      // 修改资料对话框的表单对象
      newUserInfo: {
        userid: '',
        username: '',
        telephone: '',
        email: '',
        rolename: ''
      },
      // 修改资料对话框的表单对象的验证规则
      newUserInfoRule: {
        username: [
          { required: true, message: '请输入昵称', trigger: 'blur' },
          {
            min: 2,
            max: 16,
            message: '长度在 2 到 16 个字符',
            trigger: 'blur'
          },
          { validator: checkUsername, trigger: 'blur' }
        ],
        telephone: [
          { required: true, message: '请输入电话', trigger: 'blur' },
          { validator: checkTelephone, trigger: 'blur' }
        ],
        email: [
          { required: true, message: '请输入邮箱', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' }
        ]
      }
    }
  },
  created() {
    this.getUserInfo()
    this.getAvatarList()
  },
  methods: {
    // 获取用户信息
    getUserInfo() {
      this.ruyiBlogToken = window.sessionStorage.getItem('ruyiBlogToken')
      this.ruyiBlogUserId = window.sessionStorage.getItem('ruyiBlogUserId')
      if (!this.ruyiBlogToken || this.ruyiBlogToken === '') {
        return (this.isLogin = true)
      }
      this.userInfo.id = window.sessionStorage.getItem('ruyiBlogUserId')
      this.userInfo.userid = window.sessionStorage.getItem('ruyiBlogUserUserid')
      this.userInfo.username = window.sessionStorage.getItem('ruyiBlogUsername')
      this.userInfo.userpic = window.sessionStorage.getItem('ruyiBlogUserPic')
      this.userInfo.rolename = window.sessionStorage.getItem(
        'ruyiBlogUserRolename'
      )
      this.userInfo.telephone = window.sessionStorage.getItem(
        'ruyiBlogUserTelephone'
      )
      this.userInfo.email = window.sessionStorage.getItem('ruyiBlogUserEmail')
      // console.log(this.userInfo)
    },
    // 点击按钮跳转登录界面
    gotoLogin() {
      const lastPath = this.$route.path
      window.sessionStorage.setItem('ruyiBlogLastPath', lastPath)
      this.$router.push('/login')
    },
    // 获取头像列表
    getAvatarList() {
      this.avatarList = userpicPath.userpic
      // console.log(this.avatarList)
    },
    // 选择要更换的头像
    selectAvatar(e) {
      // 保存所选头像的路径
      const reg = /\/img.*?(.jpeg|.jpg)/
      this.selectNewUserpic = e.target.src.match(reg)[0]
      // console.log(this.selectNewUserpic)
      const imgList = this.$refs.imgRef
      // 为每张图片添加索引
      for (let i = 0; i < imgList.length; i++) {
        imgList[i].index = i
      }
      // 获取当前鼠标点击的图片的索引
      // console.log(e.target)
      // console.log(e.target.index)
      const s = e.target.index
      const selectedList = this.$refs.selectedRef
      // 初始化未选中的图片的状态
      for (let j = 0; j < selectedList.length; j++) {
        selectedList[j].style.display = 'none'
      }
      selectedList[s].style.display = 'block'
    },
    // 取消头像选择
    selectedCancel() {
      const selectedList = this.$refs.selectedRef
      for (let i = 0; i < selectedList.length; i++) {
        selectedList[i].style.display = 'none'
      }
      this.selectNewUserpic = ''
    },
    // 更换头像对话框取消按钮的点击事件
    cancelAvatar() {
      this.selectedCancel()
      this.changeAvatarVisible = false
    },
    // 点击按钮更换头像
    async changeAvatar() {
      if (this.selectNewUserpic === '') {
        return this.$message.error('请选择一个头像')
      }
      const id = window.sessionStorage.getItem('ruyiBlogUserId')
      const ruyiBlogUserUserid =
        window.sessionStorage.getItem('ruyiBlogUserUserid')
      // 使用axios.all([请求1, 请求2])发送两个异步请求
      // 请求1：更换头像请求
      // 请求2：更换头像后更新留言数据表里的头像数据的请求
      this.$http
        .all([
          this.$api.userApi.changeAvatar(id, {
            user_pic: this.selectNewUserpic
          }),
          this.$api.messageApi.updateUserpic({
            userpic: this.selectNewUserpic,
            userid: ruyiBlogUserUserid
          })
        ])
        .then((res) => {
          // console.log(res)
          if (
            res[0].data.meta.status !== 200 &&
            res[1].data.res.meta.status !== 200
          ) {
            return this.$message.error('数据异常')
          }
        })
      this.$message.success('更换成功')
      window.sessionStorage.setItem('ruyiBlogUserPic', this.selectNewUserpic)
      this.changeAvatarVisible = false
      location.reload()
    },
    // 点击修改资料按钮打开修改资料对话框
    showeditUserInfoDialog() {
      this.newUserInfo.username = this.userInfo.username
      this.newUserInfo.telephone = this.userInfo.telephone
      this.newUserInfo.email = this.userInfo.email
      this.newUserInfo.rolename = this.userInfo.rolename
      // console.log(this.newUserInfo)
      this.editUserInfoDialogVisible = true
    },
    // 监听修改资料对话框的关闭事件
    editUserInfoDialogClose() {
      this.$refs.newUserInfoRef.resetFields()
      this.editUserInfoDialogVisible = false
    },
    // 点击按钮更新用户信息
    updateUserInfo() {
      const id = window.sessionStorage.getItem('ruyiBlogUserId')
      this.newUserInfo.userid =
        window.sessionStorage.getItem('ruyiBlogUserUserid')
      // console.log(this.newUserInfo)
      this.$refs.newUserInfoRef.validate((valid) => {
        // console.log(valid)
        if (!valid) {
          return this.$message.error('校验失败')
        }
        if (
          this.newUserInfo.username === this.userInfo.username &&
          this.newUserInfo.telephone === this.userInfo.telephone &&
          this.newUserInfo.email === this.userInfo.email
        ) {
          return this.$message.error('您未修改任何资料')
        }
        // 如果用户完善了资料则将用户的角色给改为会员
        if (
          this.newUserInfo.rolename === '游客' &&
          this.newUserInfo.telephone &&
          this.newUserInfo.email
        ) {
          this.newUserInfo.rolename = '会员'
        } else {
          this.newUserInfo.rolename = this.userInfo.rolename
        }
        // 校验成功，发起请求
        this.$http
          .all([
            this.$api.userApi.editUserInfo(id, this.newUserInfo),
            this.$api.messageApi.updateUsername(this.newUserInfo)
          ])
          .then((results) => {
            // console.log(results)
            if (results[0].data.meta.status !== 200) {
              return this.$message.error(results[0].data.meta.msg)
            }
            if (results[1].data.meta.status !== 200) {
              return this.$message.error(results[1].data.meta.msg)
            }
            window.sessionStorage.setItem(
              'ruyiBlogUsername',
              this.newUserInfo.username
            )
            window.sessionStorage.setItem(
              'ruyiBlogUserTelephone',
              this.newUserInfo.telephone
            )
            window.sessionStorage.setItem(
              'ruyiBlogUserEmail',
              this.newUserInfo.email
            )
            window.sessionStorage.setItem(
              'ruyiBlogUserRolename',
              this.newUserInfo.rolename
            )
            // this.userInfo.username = this.newUserInfo.username
            // this.userInfo.telephone = this.newUserInfo.telephone
            // this.userInfo.email = this.newUserInfo.email
            this.$message.success('用户资料已更新')
            this.editUserInfoDialogVisible = false
            location.reload()
          })
      })
    }
  }
}
</script>

<style lang="less" scoped>
.noLoginTipBox {
  text-align: center;
  position: absolute;
  left: 50%;
  top: 40%;
  transform: translate(-50%, -50%);
  .noLoginTips {
    font-size: 24px;
    margin: 0;
  }
  .gotoLoginBtn {
    color: aquamarine;
    margin-top: 50px;
    font-size: 20px;
    span {
      cursor: pointer;
      text-decoration: underline;
    }
  }
}
.userInfoBox {
  margin-top: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  .asideBox {
    width: 50%;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    align-items: center;
    .userpicBox {
      width: 400px;
      height: 400px;
      background-color: #000;
      border-radius: 50%;
      img {
        width: 400px;
        height: 400px;
        border-radius: 50%;
      }
    }
    .editBtnBox {
      width: 400px;
      margin-top: 30px;
      text-align: center;
    }
  }
  .mainBox {
    width: 50%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    .userInfo {
      width: 400px;
      height: 400px;
      display: flex;
      flex: 0 0 auto;
      flex-wrap: wrap;
      align-content: center;
      div {
        width: 400px;
        font-size: 20px;
        margin-top: 20px;
      }
    }
    .infoEditBtnBox {
      width: 400px;
      text-align: left;
      margin-top: 30px;
    }
  }
}
.avatarList {
  margin: 30px 30px;
  display: flex;
  flex-wrap: wrap;
  align-content: space-around;
  .avatarBox {
    width: 100px;
    height: 100px;
    background-color: #fff;
    border-radius: 50%;
    margin: 10px;
    cursor: pointer;
    position: relative;
    img {
      width: 100px;
      height: 100px;
      border-radius: 50%;
    }
    .selected {
      width: 100px;
      height: 100px;
      background-color: #dfdddd;
      border-radius: 50%;
      border: 5px solid aquamarine;
      opacity: 50%;
      display: none;
      position: absolute;
      left: -5px;
      top: -5px;
    }
  }
}
</style>
