// 留言模块的请求处理函数
// 导入请求模块
import request from '../util/request.js'
// 导入表单数据处理模块
import qs from 'qs'

// 获取用户留言
function getMessageData() {
  return request.get('/api/getMessageData')
}

// 创建留言
function createMessage(obj) {
  return request.post('/api/createMessage', qs.stringify(obj))
}

// 删除留言
function deleteMessage(id) {
  return request.post('/api/deleteMessage?id=' + id)
}

// 更新用户头像
function updateUserpic(obj) {
  return request.post('/api/updateUserpic', qs.stringify(obj))
}

// 更新用户名
function updateUsername(obj) {
  return request.post('/api/updateUsername', qs.stringify(obj))
}

export default {
  getMessageData,
  createMessage,
  deleteMessage,
  updateUserpic,
  updateUsername
}
