<template>
  <div>
    <!-- 轮播图 -->
    <el-carousel :interval="5000" arrow="always">
      <el-carousel-item v-for="item in 3" :key="item">
        <h3>欢迎访问我的博客</h3>
      </el-carousel-item>
    </el-carousel>
    <div class="category">
      <span
        class="categoryName"
        v-for="item in articleCateList"
        :key="item.id"
        @click="toStudyNotes('/' + item.alias)"
        >{{ item.name }}</span
      >
    </div>
    <!-- 卡片视图 -->
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>最近更新</span>
      </div>
      <el-table :data="LastArticleList" style="width: 100%">
        <el-table-column prop="title"> </el-table-column>
        <el-table-column prop="pub_date" width="100"> </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 笔记分类列表接收数组
      articleCateList: [],
      // 最近更新的笔记列表接收数组
      LastArticleList: []
    }
  },
  created() {
    // 设置页面标题（已有路由守卫动态设置，故此行代码废弃）
    // document.title = '如逸O的个人博客——首页'
    this.getArticleCate()
    this.getLastArticleList()
  },
  methods: {
    // 获取笔记分类
    async getArticleCate() {
      const { data: res } = await this.$http.get('/api/cates')
      // console.log(res)
      this.articleCateList = res.data
    },
    // 获取最近更新的笔记文章列表
    async getLastArticleList() {
      const { data: res } = await this.$http.get('/api/getArticle')
      // console.log(res)
      if (res.meta.status !== 200) {
        return this.$message.errorr('网络异常')
      }
      this.LastArticleList = res.data.slice(0, 10)
    },
    // 跳转至学习笔记页面
    toStudyNotes(activePath) {
      window.sessionStorage.setItem('activePath', activePath)
      this.$router.push('/studynotes')
    }
  }
}
</script>

<style lang="less" scoped>
.el-carousel {
  width: 1200px;
  height: 300px;
  z-index: 0;
  margin-top: 0;
  overflow: hidden;
  .el-carousel__item {
    z-index: 0;
    width: 1200px;
    height: 300px;
    margin-left: 0;
    // position: absolute;
    // top: 0;
    // left: 0;
    // transform: translate(0, 0);
    display: flex;
    justify-content: center;
    align-items: center;
    h3 {
      width: 1200px;
      height: 280px;
      color: #475669;
      font-size: 50px;
      letter-spacing: 5px;
      opacity: 0.75;
      line-height: 300px;
      display: flex;
      justify-content: center;
      align-items: center;
      // margin: 0;
    }
  }
}
.el-carousel__item:nth-child(3) {
  background-color: #1da0ec;
}
.el-carousel__item:nth-child(4) {
  background-color: #eca41d;
}
.el-carousel__item:nth-child(5) {
  background-color: #cfeb35;
}
.category {
  width: 1200px;
  height: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  .categoryName {
    width: auto;
    height: auto;
    padding: 10px;
    margin: 10px;
    border-radius: 25px;
    font-size: 30px;
    cursor: pointer;
  }
  .categoryName:nth-of-type(n) {
    background-color: #1da0ec;
  }
  .categoryName:nth-of-type(2n) {
    background-color: #cfeb35;
  }
  .categoryName:nth-of-type(3n) {
    background-color: #eca41d;
  }
}
.box-card {
  width: 1198px;
  margin-bottom: 40px;
  .clearfix {
    font-size: 20px;
    letter-spacing: 3px;
    // font-weight: 20px !important;
  }
  .text {
    font-size: 18px;
  }

  .item {
    margin-bottom: 18px;
  }
}
</style>
