<template>
  <el-container class="home-container">
    <!-- 侧边栏 -->
    <el-aside width="200px">
      <!-- <div class="toggle-button" @click="toggleCollapse">|||</div> -->
      <h3 class="cateTitle">笔记分类</h3>
      <!-- 侧边栏菜单区域 -->
      <el-menu
        background-color="#333744"
        text-color="#fff"
        active-text-color="#409EFF"
        :unique-opened="true"
        :collapse="isCollaspe"
        :collapse-transition="false"
        :router="false"
        :default-active="activePath"
      >
        <!-- 二级菜单 -->
        <el-menu-item
          :index="'/' + item.alias"
          v-for="item in menuList"
          :key="item.id"
          @click="saveNavStatus('/' + item.alias)"
        >
          <!-- 二级菜单的模板区域 -->
          <template slot="title">
            <!-- 图标 -->
            <i class="el-icon-menu"></i>
            <!-- 文本 -->
            <span>{{ item.name }}</span>
          </template>
        </el-menu-item>
      </el-menu>
    </el-aside>
    <!-- 右侧内容主体 -->
    <el-main>
      <!-- 路由占位符 -->
      <!-- <router-view></router-view> -->
      <!-- 卡片视图 -->
      <el-card>
        <!-- 搜索与添加区域 -->
        <el-row :gutter="20" type="flex" justify="center">
          <el-col :span="12">
            <el-input
              placeholder="请输入内容"
              v-model="queryInfo.query"
              clearable
              @clear="defaultLoad"
            >
              <el-button
                slot="append"
                icon="el-icon-search"
                @click="defaultLoad"
              ></el-button>
            </el-input>
          </el-col>
        </el-row>
        <!-- 文章列表区域 -->
        <el-table :data="articleList" style="width: 100%">
          <el-table-column type="index" width="100"> </el-table-column>
          <el-table-column prop="title">
            <!-- 作用域插槽 -->
            <template slot-scope="scope">
              <span
                class="previewLink"
                @click="showPreviewDialog(scope.row.id)"
                >{{ scope.row.title }}</span
              >
            </template>
          </el-table-column>
          <el-table-column prop="pub_date" width="180"> </el-table-column>
        </el-table>
        <!-- 分页区域 -->
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="queryInfo.pagenum"
          :page-sizes="[10, 15, 20, 50]"
          :page-size="queryInfo.pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
      </el-card>
    </el-main>
  </el-container>
</template>

<script>
export default {
  data() {
    return {
      // 菜单数据接收数组
      menuList: [],
      // 图标数据
      iconObj: {
        1: 'iconfont icon-users',
        2: 'iconfont icon-tijikongjian',
        3: 'iconfont icon-shangpin',
        102: 'iconfont icon-danju',
        145: 'iconfont icon-baobiao'
      },
      // 左侧菜单是否折叠
      isCollaspe: false,
      // 被激活的链接地址
      activePath: '',
      // 查询条件对象
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10
      },
      // 文章总数
      total: 0,
      // 全部文章数据
      articleAll: [],
      // 文章列表数据
      articleList: []
    }
  },
  created() {
    this.getMenuList()
    this.getArticleList()
    this.activePath = window.sessionStorage.getItem('activePath')
    this.queryInfo.query = window.sessionStorage
      .getItem('activePath')
      .substring(1)
      .toUpperCase()
  },
  methods: {
    // 控制左侧菜单的收缩
    toggleCollapse() {
      this.isCollaspe = !this.isCollaspe
    },
    // 获取菜单列表
    async getMenuList() {
      const { data: res } = await this.$http.get('/api/cates')
      // console.log(res)
      if (res.meta.status !== 200) {
        return this.$message.error('网络异常')
      }
      this.menuList = res.data
    },
    // 保存链接的激活状态并查找该分类的文章
    saveNavStatus(activePath) {
      window.sessionStorage.setItem('activePath', activePath)
      this.activePath = activePath
      this.queryInfo.query = activePath.substring(1).toUpperCase()
      // console.log(this.queryInfo.query)
      this.getArticleList()
    },
    // 默认加载页面
    defaultLoad() {
      window.sessionStorage.setItem('activePath', '')
      this.activePath = ''
      this.getArticleList()
    },
    // 获取文章列表
    async getArticleList() {
      const { data: res } = await this.$http.get('/api/getPublishedArticls')
      // console.log(res)
      if (res.meta.status !== 200) {
        return this.$message.error('网络异常')
      }
      // this.articleList = res.data
      // 1.1查询函数，将符合查询条件的数据放入展示数组
      function queryfn(arr, query) {
        let queryData = []
        if (query === '') {
          queryData = arr
          return queryData
        } else {
          for (var i in arr) {
            if (arr[i].cate.includes(query) || arr[i].title.includes(query)) {
              queryData.push(arr[i])
            }
          }
        }
        return queryData
      }
      this.articleAll = queryfn(res.data, this.queryInfo.query)
      this.total = this.articleAll.length
      // 1.2数据分页函数
      function slicefn(arr1, arr2, pagenum, pagesize) {
        arr2 = arr1.slice((pagenum - 1) * pagesize, pagenum * pagesize)
        return arr2
      }
      this.articleList = slicefn(
        this.articleAll,
        this.articleList,
        this.queryInfo.pagenum,
        this.queryInfo.pagesize
      )
    },
    // 监听pagesize改变的事件
    handleSizeChange(size) {
      this.queryInfo.pagesize = size
      this.getArticleList()
    },
    // 监听页码值改变的事件
    handleCurrentChange(page) {
      this.queryInfo.pagenum = page
      this.getArticleList()
    },
    // 存储所选文章id并跳转文章页面
    showPreviewDialog(id) {
      window.sessionStorage.setItem('noteId', id)
      const { routeData } = this.$router.resolve({
        name: 'notecontent',
        query: {
          noteId: id
        }
      })
      window.open('http://127.0.0.1:3007/#/notecontent', '_blank')
      // window.open(routeData, '_blank')
      // console.log('新增页面')
    }
  }
}
</script>

<style lang="less" scoped>
.home-container {
  min-height: 750px;
  height: 100%;
}
// .el-header {
//   background-color: #373d41;
//   display: flex;
//   justify-content: space-between;
//   padding-left: 0;
//   align-items: center;
//   color: #fff;
//   font-size: 20px;
//   > div {
//     display: flex;
//     align-items: center;
//     span {
//       margin-left: 15px;
//     }
//   }
// }
.el-aside {
  background-color: #333744;
  min-height: 750px;
  .cateTitle {
    text-align: center;
    color: #fff;
  }
  .el-menu {
    border-right: none;
  }
}
.el-main {
  background-color: #eaedf1;
  min-height: 750px;
}
.iconfont {
  margin-right: 10px;
}
.toggle-button {
  background-color: #4a5064;
  font-size: 10px;
  line-height: 24px;
  color: #fff;
  text-align: center;
  letter-spacing: 2px;
  cursor: pointer;
}
.previewLink {
  cursor: pointer;
}
</style>
