<template>
  <div class="bodyContainer">
    <!-- 用户登录界面 -->
    <div class="loginBox" v-show="!regUserDialogVisible">
      <div class="titleBox"><span>如逸O的个人博客</span></div>
      <!-- 登陆表单区域 -->
      <el-form
        :model="loginForm"
        ref="loginFormRef"
        :rules="loginFormRules"
        label-width="0px"
        class="loginForm"
      >
        <!-- 用户名 -->
        <el-form-item prop="username">
          <el-input
            v-model="loginForm.username"
            prefix-icon="el-icon-user-solid"
            clearable
          ></el-input>
        </el-form-item>
        <!-- 密码 -->
        <el-form-item prop="password">
          <el-input
            v-model="loginForm.password"
            prefix-icon="iconfont icon-3702mima"
            type="password"
            clearable
          ></el-input>
        </el-form-item>
        <!-- 按钮区域 -->
        <div class="btns">
          <span class="btnShowRegUser" @click="showRegUserDialog"
            >注册账号</span
          >
          <div>
            <el-button type="info" @click="resetloginForm">重置</el-button>
            <el-button type="primary" @click="login">登录</el-button>
          </div>
        </div>
      </el-form>
    </div>
    <!-- 用户注册界面 -->
    <div class="regUserBox" v-show="regUserDialogVisible">
      <div class="titleBox"><span>如逸O的个人博客</span></div>
      <!-- 用户注册表单区域 -->
      <el-form
        ref="regUserFormRef"
        :model="regUserForm"
        :rules="loginFormRules"
        label-width="80px"
        hide-required-asterisk
        class="regUserForm"
      >
        <el-form-item label="用户名:" prop="username">
          <el-input
            v-model="regUserForm.username"
            placeholder="请输入用户名"
            clearable
          ></el-input>
        </el-form-item>
        <el-form-item label="密码:" prop="password">
          <el-input
            v-model="regUserForm.password"
            type="password"
            placeholder="请输入密码"
            clearable
          ></el-input>
        </el-form-item>
        <el-form-item label="确认密码:" prop="password">
          <el-input
            v-model="regUserForm.password01"
            type="password"
            placeholder="请再次输入密码"
            clearable
          ></el-input>
        </el-form-item>
        <!-- 按钮区域 -->
        <div class="regBtns">
          <span @click="regUserDialogVisible = false" class="btnBack"
            >&lt;&lt;返回</span
          >
          <div>
            <el-button type="info" @click="resetRegUserForm">重置</el-button>
            <el-button type="primary" @click="regUser">注册</el-button>
          </div>
        </div>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 登录表单的数据对象
      loginForm: {
        username: '',
        password: ''
      },
      // 登录表单的数据验证规则
      loginFormRules: {
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          { min: 2, max: 16, message: '长度在 2 到 16 个字符', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 6, max: 16, message: '长度在 6 到 16 个字符', trigger: 'blur' }
        ]
      },
      // 控制注册用户对话框的显示与隐藏
      regUserDialogVisible: false,
      // 注册用户表单的数据对象
      regUserForm: {
        username: '',
        password: '',
        password01: ''
      }
    }
  },
  created() {},
  methods: {
    // 点击按钮发起登录请求
    async login() {
      const { data: res } = await this.$http.post(
        '/api/login',
        this.$qs.stringify(this.loginForm)
      )
      // console.log(res)
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.$message.success(res.meta.msg)
      const token = res.token
      const ruyiBlogUsername = res.data.username
      const ruyiBlogUserPic = res.data.user_pic
      const ruyiBlogUserId = res.data.id
      const ruyiBlogUserUserid = res.data.userid
      const ruyiBlogUserEmail = res.data.email
      const ruyiBlogUserRolename = res.data.rolename
      const ruyiBlogUserTelephone = res.data.telephone
      window.sessionStorage.setItem('ruyiBlogToken', token)
      window.sessionStorage.setItem('ruyiBlogUserId', ruyiBlogUserId)
      window.sessionStorage.setItem('ruyiBlogUserUserid', ruyiBlogUserUserid)
      window.sessionStorage.setItem('ruyiBlogUsername', ruyiBlogUsername)
      window.sessionStorage.setItem('ruyiBlogUserPic', ruyiBlogUserPic)
      window.sessionStorage.setItem('ruyiBlogUserEmail', ruyiBlogUserEmail)
      window.sessionStorage.setItem(
        'ruyiBlogUserRolename',
        ruyiBlogUserRolename
      )
      window.sessionStorage.setItem(
        'ruyiBlogUserTelephone',
        ruyiBlogUserTelephone
      )
      const lastPath = window.sessionStorage.getItem('ruyiBlogLastPath')
      if (!lastPath || lastPath === '') {
        this.$router.push('/welcome')
      } else {
        this.$router.push(lastPath)
      }
    },
    // 点击按钮清空登录表单数据
    resetloginForm() {
      this.$refs.loginFormRef.resetFields()
    },
    // 点击按钮弹出注册用户对话框
    showRegUserDialog() {
      this.regUserDialogVisible = true
      this.$refs.regUserFormRef.resetFields()
    },
    // 点击按钮清空注册表单数据
    resetRegUserForm() {
      this.$refs.regUserFormRef.resetFields()
    },
    // 点击按钮注册用户
    async regUser() {
      if (this.regUserForm.password !== this.regUserForm.password01) {
        return this.$message.error('密码不一致')
      }
      const { data: res } = await this.$http.post(
        '/api/reguser',
        this.$qs.stringify(this.regUserForm)
      )
      if (res.meta.status !== 201) return this.$message.error(res.meta.msg)
      this.$message.success('注册成功')
      this.loginForm.username = this.regUserForm.username
      this.loginForm.password = ''
      this.regUserDialogVisible = false
    }
  }
}
</script>

<style lang="less" scoped>
.bodyContainer {
  width: 100%;
  height: 100%;
  background-color: aquamarine;
}
.loginBox {
  width: 400px;
  height: 500px;
  background-color: #fff;
  border-radius: 3px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.titleBox {
  text-align: center;
  margin-top: 100px;
  span {
    font-size: 20px;
    letter-spacing: 5px;
    padding: 6px;
    border-top: 2px solid #000;
    border-bottom: 2px solid #000;
  }
}
.loginForm {
  width: 100%;
  padding: 30px;
  margin-top: 100px;
  box-sizing: border-box;
}
.btns {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.btnShowRegUser {
  text-decoration: underline;
  cursor: pointer;
  color: cyan;
  font-size: 16px;
}
.regUserBox {
  width: 400px;
  height: 500px;
  background-color: #fff;
  border-radius: 3px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.regUserForm {
  width: 100%;
  padding: 30px 40px 30px 30px;
  box-sizing: border-box;
  position: absolute;
  bottom: 0;
}
.btnBack {
  text-decoration: underline;
  cursor: pointer;
  color: cyan;
  font-size: 16px;
}
.regBtns {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
</style>
