import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../components/Home/Home.vue'
import Welcome from '../components/Home/Welcome.vue'
import Vitae from '../components/Vitae/Vitae.vue'
import DailyTask from '../components/DailyTask/DailyTask.vue'
import StudyNotes from '../components/StudyNotes/StudyNotes.vue'
import MessageBoard from '../components/MessageBoard/MessageBoard.vue'
import PresentTask from '../components/DailyTask/PresentTask.vue'
import TaskHistory from '../components/DailyTask/TaskHistory.vue'
// import NoteList from '../components/StudyNotes/NoteHome.vue'
import NoteContent from '../components/StudyNotes/NoteContent.vue'
import Login from '../components/Login/Login.vue'
import PersonalCenter from '../components/PersonalCenter/PersonalCenter.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/welcome'
  },
  {
    path: '/home',
    name: 'home',
    component: Home,
    redirect: '/welcome',
    children: [
      {
        path: '/welcome',
        name: 'welcome',
        component: Welcome,
        meta: { title: '如逸O的个人博客——首页' }
      },
      {
        path: '/vitae',
        name: 'vitae',
        component: Vitae,
        meta: { title: '如逸O的个人博客——简历' }
      },
      {
        path: '/dailytask',
        name: 'dailytask',
        component: DailyTask,
        meta: { title: '如逸O的个人博客——每日任务' },
        redirect: '/presenttask',
        children: [
          {
            path: '/presenttask',
            name: 'presenttask',
            component: PresentTask,
            meta: { title: '如逸O的个人博客——今日任务' }
          },
          {
            path: '/taskhistory',
            name: 'taskhistory',
            component: TaskHistory,
            meta: { title: '如逸O的个人博客——历史完成度' }
          }
        ]
      },
      {
        path: '/studynotes',
        name: 'studynotes',
        component: StudyNotes,
        meta: { title: '如逸O的个人博客——学习笔记' }
        // redirect: '/notelist',
        // children: [{
        //   path: '/notelist',
        //   name: 'NoteList',
        //   component: NoteList,
        //   meta: { title: '如逸O的个人博客——学习笔记' }
        // }]
      },
      {
        path: '/messageboard',
        name: 'messageboard',
        component: MessageBoard,
        meta: { title: '如逸O的个人博客——留言板' }
      },
      {
        path: '/notecontent',
        name: 'notecontent',
        component: NoteContent,
        meta: { title: '如逸O的个人博客——笔记内容' }
      },
      {
        path: '/personalcenter',
        name: 'personalcenter',
        component: PersonalCenter,
        meta: { title: '如逸O的个人博客——个人中心' }
      }
    ]
  },
  {
    path: '/login',
    name: 'login',
    component: Login,
    meta: { title: '如逸O的个人博客——Login' }
  }
  // {
  //   path: '/notecontent',
  //   name: 'NoteContent',
  //   component: NoteContent,
  //   meta: { title: '如逸O的个人博客——笔记内容' }
  // }
  // {
  //   path: '/about',
  //   name: 'About',
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  // }
]

const router = new VueRouter({
  routes
})

// 路由守卫
// beforeEach是router的钩子函数，在进入路由前执行
// eslint-disable-next-line no-use-before-define
router.beforeEach((to, from, next) => {
  // 判断是否有标题并动态设置标题
  if (to.meta.title) {
    document.title = to.meta.title
  } else {
    document.title = '如逸O的个人博客'
  }
  // 执行进入路由，如果不写就不会进入目标页
  next()
})

export default router
