// 用户模块的请求处理函数

// 导入请求模块
import request from '../util/request.js'
// 导入表单数据处理模块
import qs from 'qs'

// 通过id获取用户信息
function getUserInfoById(id) {
  return request.post('/api/getUser?id=' + id)
}

// 更换头像
function changeAvatar(id, obj) {
  return request.post('/api/changeAvatar?id=' + id, qs.stringify(obj))
}

// 修改用户信息
function editUserInfo(id, obj) {
  return request.post('/api/editUserInfo?id=' + id, qs.stringify(obj))
}

export default {
  getUserInfoById,
  changeAvatar,
  editUserInfo
}
