<template>
  <el-container class="home-container">
    <!-- 头部区域 -->
    <el-header>
      <div class="navBar">
        <span>如逸o的个人博客</span>
        <div class="rightBar">
          <!-- 菜单区域 -->
          <el-menu
            :default-active="this.$route.path"
            class="el-menu-demo"
            mode="horizontal"
            background-color="#dfdddd"
            text-color="#000"
            active-text-color="#409eff"
            :router="true"
          >
            <el-menu-item index="/welcome">首页</el-menu-item>
            <el-menu-item index="/vitae">个人介绍</el-menu-item>
            <!-- <el-submenu index="2">
            <template slot="title">我的工作台</template>
            <el-menu-item index="2-1">选项1</el-menu-item>
            <el-menu-item index="2-2">选项2</el-menu-item>
            <el-menu-item index="2-3">选项3</el-menu-item>
            <el-submenu index="2-4">
              <template slot="title">选项4</template>
              <el-menu-item index="2-4-1">选项1</el-menu-item>
              <el-menu-item index="2-4-2">选项2</el-menu-item>
              <el-menu-item index="2-4-3">选项3</el-menu-item>
            </el-submenu>
          </el-submenu> -->
            <!-- <el-menu-item index="/dailytask">修身自律</el-menu-item> -->
            <el-menu-item index="/studynotes" @click="refreshStudynotes"
              >学习笔记</el-menu-item
            >
            <el-menu-item index="/messageboard">留言</el-menu-item>
            <!-- 禁用模式示例 -->
            <!-- <el-menu-item index="3" disabled>消息中心</el-menu-item> -->
          </el-menu>
          <!-- 下拉菜单 -->
          <el-dropdown @command="handleCommand">
            <!-- 用户头像区域 -->
            <div class="userPic" @click="goLogin">
              <img :src="userpic" alt="" />
            </div>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item command="1">个人中心</el-dropdown-item>
              <el-dropdown-item command="2">注销登录</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </div>
    </el-header>
    <!-- 页面主体区域 -->
    <el-main>
      <!-- 路由占位符 -->
      <router-view></router-view>
    </el-main>
    <!-- 底栏容器 -->
    <el-footer>
      <div>
        <a href="https://beian.miit.gov.cn" target="_blank"
          >赣ICP备2022001495号-1</a
        >
      </div>
    </el-footer>
  </el-container>
</template>

<script>
import userpicPath from '../../assets/img/userPic/userpicPath'

export default {
  data() {
    return {
      activeIndex2: '1',
      // 头像列表接收对象
      userpicList: {},
      // 导航栏显示的用户头像的地址
      userpic: '',
      ruyiBlogToken: '',
      // 用户存储在服务器数据库里的地址
      ruyiBlogUserpic: ''
    }
  },
  created() {
    this.showUserpic()
  },
  methods: {
    // 点击按钮清空缓存的activePath
    // routerSwitch() {
    //   // console.log(key, keyPath)
    //   window.sessionStorage.setItem('activePath', '')
    // },
    // 点击按钮刷新学习笔记页面
    refreshStudynotes() {
      const activePath = window.sessionStorage.getItem('activePath')
      // 如果activePath为空则不需要刷新页面
      if (activePath === '') {
        return
      }
      window.sessionStorage.setItem('activePath', '')
      // 原地页面刷新
      // // 方法一
      // this.$router.go(0)
      // 方法二
      location.reload()
    },
    // 用户头像的显示
    showUserpic() {
      this.userpicList = userpicPath.userpic
      this.ruyiBlogToken = window.sessionStorage.getItem('ruyiBlogToken')
      this.ruyiBlogUserpic = window.sessionStorage.getItem('ruyiBlogUserPic')
      // console.log(this.userpicList)
      if (!this.ruyiBlogToken) {
        this.userpic = this.userpicList.userpic00
      } else {
        if (
          this.ruyiBlogUserpic === '' ||
          this.ruyiBlogUserpic === null ||
          this.ruyiBlogUserpic === 'null'
        ) {
          this.userpic = this.userpicList.userpic01
        } else {
          this.userpic = this.ruyiBlogUserpic
        }
      }
      // console.log(this.userpic)
    },
    // 点击头像区域或下拉菜单里的个人中心按钮跳转界面
    goLogin() {
      if (!this.ruyiBlogToken) {
        const ruyiBlogLastPath = this.$route.path
        window.sessionStorage.setItem('ruyiBlogLastPath', ruyiBlogLastPath)
        this.$router.push('/login')
      } else {
        this.$router.push('/personalcenter')
      }
    },
    // 下拉菜单的点击事件
    handleCommand(command) {
      switch (command) {
        case '1':
          // 指令1，前往个人中心
          this.goLogin()
          break
        case '2':
          // 指令2,注销登录
          // 移除sessionStorage里存储的token
          window.sessionStorage.removeItem('ruyiBlogToken')
          // 重新加载页面
          location.reload()
      }
    }
  }
}
</script>

<style lang="less" scoped>
.el-container {
  width: 100%;
  // height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.el-header {
  width: 100%;
  height: 100px !important;
  z-index: 99;
  background-color: #dfdddd;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  > .navBar {
    width: 1200px;
    background-color: #dfdddd;
    display: flex;
    justify-content: space-between;
    align-items: center;
    span {
      width: 200px;
      height: 50px;
      font-size: 18px;
      letter-spacing: 5px;
      color: #000;
      border-bottom: 2px solid #000;
      border-top: 2px solid #000;
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
}
.rightBar {
  height: 54px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: nowrap;
}
.el-menu {
  .el-menu-item {
    width: 130px;
    height: 54px;
    font-size: 18px;
    letter-spacing: 3px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: nowrap;
    margin-right: 0;
  }
  .el-menu-item:hover {
    background-color: #dfdddd !important;
    color: #409eff;
  }
}
.userPic {
  width: 54px;
  height: 54px;
  background-color: #000;
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  img {
    width: 54px;
    height: 54px;
    border-radius: 50%;
  }
}
.el-main {
  width: 1200px;
  height: auto;
  min-height: 736px;
  margin-top: 100px;
  padding: 0;
  background-color: #fff;
}
.el-footer {
  width: 100%;
  height: 100px !important;
  background: #dfdddd;
  display: flex;
  justify-content: center;
  div {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 16px;
    a {
      text-decoration: none;
    }
  }
}
</style>
