<template>
  <div class="baseContainer">
    <!-- 用户留言区域 -->
    <el-card class="lamArea">
      <el-row type="flex" justify="space-around" class="lamAreaRow">
        <el-col :span="20"
          ><el-input
            v-model="messageForm.content"
            clearable
            maxlength="200"
            show-word-limit
            >></el-input
          >
        </el-col>
        <el-col :span="2"
          ><el-button type="primary" @click="submitMessage"
            >留言</el-button
          ></el-col
        >
      </el-row>
    </el-card>
    <!-- 主体内容区域 -->
    <div class="contentContainer">
      <!-- 用户留言内容区域 -->
      <el-card class="messageCard" v-for="item in messageData" :key="item.id">
        <div slot="header" class="cardHeader">
          <div class="userInfoBox">
            <div class="userPic">
              <img :src="item.userpic" alt="" />
            </div>
            <span class="username">{{ item.username }}</span>
          </div>
          <div class="lamDate">{{ item.lamDate }}</div>
        </div>
        <div class="content">{{ item.content }}</div>
      </el-card>
      <!-- 分页区域 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5, 10, 20, 50]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </div>
    <!-- 留言成功对话框 -->
    <el-dialog :visible.sync="lamDialogVisible" width="30%">
      <el-progress
        type="circle"
        :percentage="percentage"
        status="success"
      ></el-progress
      ><br />
      <span v-show="successTipVisible">留言成功</span>
    </el-dialog>
    <!-- 留言提示对话框 -->
    <el-dialog :visible.sync="loginTipDialogVisible" width="30%">
      <span>登录后才能留言哦</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="loginTipDialogVisible = false">再等等</el-button>
        <el-button type="primary" @click="gotoLogin">去登录</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 查询条件对象
      queryInfo: {
        queery: '',
        pagenum: 1,
        pagesize: 5
      },
      // 留言数据接收总数
      total: 0,
      // 留言数据接收数组
      messageDataAll: [],
      // 留言数据展示数组
      messageData: [],
      // 用户留言表单对象
      messageForm: {
        userid: '',
        username: '',
        userpic: '',
        lamDate: '',
        content: ''
      },
      token: '',
      // 控制留言成功对话框的显示与隐藏
      lamDialogVisible: false,
      // 控制登录提示对话框的显示与隐藏
      loginTipDialogVisible: false,
      // 留言进度条提示值
      percentage: 0,
      // 控制留言成功提示的显示与隐藏
      successTipVisible: false
    }
  },
  created() {
    this.getMessageData()
    this.getUserInfo()
  },
  methods: {
    // 获取用户留言
    async getMessageData() {
      const { data: res } = await this.$api.messageApi.getMessageData()
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      }
      // console.log(res)
      this.messageDataAll = res.data
      // console.log(this.messageDataAll)
      this.total = this.messageDataAll.length
      // 数据分页函数
      function slicefn(arr1, arr2, pagenum, pagesize) {
        arr2 = arr1.slice((pagenum - 1) * pagesize, pagenum * pagesize)
        return arr2
      }
      this.messageData = slicefn(
        this.messageDataAll,
        this.messageData,
        this.queryInfo.pagenum,
        this.queryInfo.pagesize
      )
      // console.log(this.messageData)
    },
    // 监听页面留言显示数量改变的事件
    handleSizeChange(pagesize) {
      this.queryInfo.pagesize = pagesize
      this.getMessageData()
    },
    // 监听页码改变的事件
    handleCurrentChange(pagenum) {
      this.queryInfo.pagenum = pagenum
      this.getMessageData()
    },
    // 获取登录用户的信息
    getUserInfo() {
      this.messageForm.userid =
        window.sessionStorage.getItem('ruyiBlogUserUserid')
      this.messageForm.username =
        window.sessionStorage.getItem('ruyiBlogUsername')
      this.messageForm.userpic =
        window.sessionStorage.getItem('ruyiBlogUserPic')
      this.token = window.sessionStorage.getItem('ruyiBlogToken')
    },
    // 点击按钮提交留言
    async submitMessage() {
      const ruyiBlogToken = window.sessionStorage.getItem('ruyiBlogToken')
      if (!ruyiBlogToken || ruyiBlogToken === '') {
        return (this.loginTipDialogVisible = true)
      }
      if (this.messageForm.content === '') {
        return this.$message.error('请输入有效内容')
      }
      const time = new Date()
      const reg = new RegExp('/', 'g')
      this.messageForm.lamDate = time.toLocaleDateString().replace(reg, '-')
      // console.log(this.messageForm.lamDate)
      const { data: res } = await this.$api.messageApi.createMessage(
        this.messageForm
      )
      // console.log(res)
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      }
      this.lamDialogVisible = true
      const fnS = setInterval(() => {
        if (this.percentage < 100) {
          this.percentage += 10
        }
        if (this.percentage === 100) {
          this.successTipVisible = true
          clearInterval(fnS)
          setTimeout(() => {
            this.lamDialogVisible = false
            // 留言成功清空留言输入框
            this.messageForm.content = ''
            this.percentage = 0
            this.successTipVisible = false
            this.getMessageData()
          }, 1000)
        }
      }, 200)
    },
    // 点击按钮跳转登录界面
    gotoLogin() {
      const ruyiBlogLastPath = this.$route.path
      window.sessionStorage.setItem('ruyiBlogLastPath', ruyiBlogLastPath)
      this.$router.push('/login')
    }
  }
}
</script>

<style lang="less" scoped>
.baseContainer {
  width: 100%;
  height: 100%;
  text-align: center;
}
.lamArea {
  width: 1198px;
  background-color: #1da0ec;
  background: url('../../assets/img/bg/lamAreaBg.jpeg') no-repeat;
  position: fixed;
  top: 100px;
  .lamAreaRow {
    margin: 30px 0;
  }
}
.contentContainer {
  margin-top: 160px;
}
.messageCard {
  margin-top: 15px;
  .cardHeader {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 18px;
    .userInfoBox {
      display: flex;
      justify-content: left;
      align-items: center;
    }
    .userPic {
      width: 54px;
      height: 54px;
      background-color: #000;
      border-radius: 50%;
      cursor: pointer;
      display: flex;
      justify-content: center;
      align-items: center;
      overflow: hidden;
      img {
        width: 54px;
        height: 54px;
        border-radius: 50%;
      }
    }
    .username {
      font-size: 18px;
      margin-left: 5px;
    }
  }
  .content {
    font-size: 18px;
    text-align: left;
  }
}
.el-pagination {
  margin-bottom: 15px;
}
</style>
